/*TT Norms*/
@font-face {
  font-family: 'TTNorms-Regular';
  src: url("../fonts/TTNorms/TTNorms-Regular.woff2") format('woff2'),
       url("../fonts/TTNorms/TTNorms-Regular.woff") format('woff'),
       url("../fonts/TTNorms/TTNorms-Regular.ttf") format('ttf');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'TTNorms-Medium';
  src: url("../fonts/TTNorms/TTNorms-Medium.woff2") format('woff2'),
       url("../fonts/TTNorms/TTNorms-Medium.woff") format('woff'),
       url("../fonts/TTNorms/TTNorms-Medium.ttf") format('ttf');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'TTNorms-Bold';
